body, html {
  overflow: hidden;
  height: 100%;
  background-color: #0a385f;
}

.App {
  text-align: center;
  height: 100%;
  overflow: hidden;
}
.cardWrapper {
  height: 100vh;
  width: 100vw;
}

.wrapper {
  width: 100%;
  overflow: hidden;
}
.display-4{
  font-weight: bold;
}

.card {
  background-color: #0a385f;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  color: white;
}

.card-body{

  width: 60%;
  
}
@media (max-width:600px) { 
  .card-body{
    width: 100%
  }
}

.car-rep {
  margin-top: 80px;
}

